/* *************************** target bet ************************** */
.target-bet-box {
  margin: 0px 0px 0px 0px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  flex-direction: column;
  gap: 20px;
}

.slidecontainer {
  padding: 38px 0px 12px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin: auto;
  gap: 30px;
  position: relative;
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 3px;
  border-radius: 5px;
  background: #999999;
  outline: none;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  /* box-shadow: 0px 0px 15px rgb(2, 255, 179); */
  -webkit-appearance: none;
  appearance: none;
  width: 28px;
  height: 28px;
  border: 0;
  border-radius: 20px;
  background: #ffffff;
  background-repeat: no-repeat;
  background-size: 30px 30px;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  /* box-shadow: 0px 0px 15px rgb(2, 255, 179); */
  width: 28px;
  height: 28px;
  border: 0;
  border-radius: 20px;
  background: #ffffff;
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: center;
  cursor: pointer;
}

.disable-not-allowed .slider::-webkit-slider-thumb, .disable-not-allowed .slider::-moz-range-thumb {
  pointer-events: all !important;
  cursor: not-allowed !important;
}

.slider-value {
  font-size: 16px;
  position: absolute;
  top: -3px;
  color: #fff;
}
input[type="range"]::-webkit-slider-thumb {
  cursor: grab;
}
input[type="range"]::-webkit-slider-thumb:active {
  cursor: grabbing;
}

