/* ************************ Mine CSS Start ******************* */

.tower-game-section.mines-game {
  background: #292929;
  overflow: hidden;
  padding: 30px;
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.tower-game-section.mines-game h6 {
  position: relative;
  color: rgb(144, 144, 144);
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  user-select: none;
  margin: 0;
}

.tower-main-box {
 
  
 
 background: #232323;
 width: auto;
  height: auto;
  display: grid;
}

.mines-game-sec {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 10px;
  height: 100%;
  width: 100%;
  user-select: none;
  cursor: default;
}

.mines-game-tiles {
  position: relative;
  display: flex;
  flex-direction: column;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  border-radius: 7px;
  border-style: solid;
  --uncovering-scale: 0.97;
  transition: transform 0s ease 0s, opacity 0s ease 0s;
  transform: scale(1);
  opacity: 1;
  cursor: default;
  border-width: 2px;
  border-color: transparent;
  background-color: rgb(68, 68, 68);
  background-size: 500% 500%;
  background-position: 0% 0%;
}

.target-tabs-data {
  display: flex;
  flex-direction: column;
  /* padding-top: 30px; */
}

.target-tabs-data .nav-tabs {
  background-color: #444444;
}

.target-tabs-data .nav-tabs {
  background-color: #444;
  border: none;
  margin-bottom: 10px;
  border-radius: 4px;
}

.target-tabs-data .nav-tabs .nav-link {
  border: none;
  border-radius: 4px;
  color: #999;
  font-size: 18px;
  font-weight: 500;
  line-height: 15px;
  max-height: 48px;
  padding: 16px 0;
  text-align: center;
  width: 100%;
}

.target-tabs-data .nav-tabs .nav-item.show .nav-link,
.target-tabs-data .nav-tabs .nav-link.active {
  background: #262626;
  border: none;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.05);
  color: #fff;
  font-weight: 500;
}


.mines-game-tiles:active {
  /* transform: translateY(0px); */
  transform: scale(0.96);
}

.mines-game .active-game-tile {
  opacity: 1 !important;
}

.mines-game .active-game-tile p {
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #a8f55c;
  max-width: 92px;
  height: 29px;
  background: rgba(168, 245, 92, 0.25);
  backdrop-filter: blur(10px);
  border-radius: 2px;
  margin: 0px auto;
  margin-bottom: 0px !important;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: relative;
  margin-top: -10px;
}

.mines-game .win-game-tile-red p {
  color: #000;
  background: rgba(231, 38, 73, 0.25);
}

.mines-game .mines-game-tiles.active-game-tile img {
  height: 55px;
  width: 55px;
}

.mines-game .mines-game-tiles.active-game-tile svg {
  width: 55px;
}

.mines-game-tiles svg {
  width: 37.35px !important;
}

.mines-game .bet-input-btn {
  height: 41px;
}

.mines-game .nav-tabs {
  background: #23252b;
  border-radius: 4px;
}

.mines-game-tiles.win-game-tile-red {
 
  cursor: default;
  border-width: 2px !important;
  border-color: rgb(153, 153, 153) !important;
  background-size: 500% 500% !important;
  background-position: 75% 100% !important;
  background-color: rgb(74, 74, 74) !important;
}
.mines-game-tiles.win-game-tile-gray {
  cursor: default !important;
  border-width: 2px !important;
  border-color: rgb(51, 51, 51) !important;
  background-size: 500% 500% !important;
  background-position: 25% 0% !important;
  background-color: rgb(51, 51, 51) !important;
}

.mines-game-tiles.win-game-tile-green {
  
  /* background: #3e6d4f !important; */
  border-radius: 7px;
  opacity: 0.8;
  pointer-events: none;
  cursor: default;
  border-width: 2px;
  border-color: rgb(119, 255, 171);
  background-size: 500% 500%;
  background-position: 0% 0%;
  /* background-color: rgb(62, 109, 79); */
}

.mines-game-tiles.win-game-tile-green .mine-diamonds {
  position: relative;
  overflow: hidden;
}

.mines-game-tiles.win-game-tile-green .mine-diamonds svg::before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  transform: skew(40deg);
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0px,
    rgba(255, 255, 255, 0.7) 90%,
    rgba(255, 255, 255, 0)
  );
  animation-name: slide;
  animation-duration: 1.5s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  opacity: 1;
}

@keyframes slide {
  0% {
    transform: skew(-40deg) translateX(-100%);
  }

  100% {
    transform: skew(-40deg) translateX(100%);
  }
}

.mines-winner-loss {
  max-width: 209px;
  width: 100%;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  padding: 3px;
  text-align: center;
  border-radius: 14px;
  background:  linear-gradient(225deg, rgb(119, 255, 168) 0%, rgb(255, 224, 129) 33%, rgb(255, 173, 142) 68%, rgb(165, 146, 255) 100%);
  box-shadow: 0 0 10px rgb(73 217 111 / 40%);
  /* transform: scale(1); */
}
.mines-winner-grid {
  background: rgb(32, 32, 32);
  padding: 35px;
  border-radius: 14px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5px;
}
.mines-winner-grid h6 {
  background-image: linear-gradient(225deg, rgb(119, 255, 168) 0%, rgb(255, 224, 129) 33%, rgb(255, 173, 142) 68%, rgb(165, 146, 255) 100%);
  background-size: 100%;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  white-space-collapse: collapse;
  text-wrap: nowrap;
  text-align: center;
  font-size: 22px;
  background-repeat: repeat;
