  background-image: linear-gradient(225deg, rgb(119, 255, 168) 0%, rgb(255, 224, 129) 33%, rgb(255, 173, 142) 68%, rgb(165, 146, 255) 100%);
  background-size: 100%;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  white-space-collapse: collapse;
  text-wrap: nowrap;
  text-align: center;
  font-size: 40px;
  letter-spacing: -2px;
  background-repeat: repeat;
  margin: 0;
}

.mines-winner-box::before {
  content: "";
  position: absolute;
  background: rgba(231, 38, 73, 0.15);
  filter: blur(90px);
  width: 113px;
  height: 113px;
  border-radius: 50%;
}

.mines-right-section {
  max-width: 100%;
  position: relative;
  width: 100%;
}

.mines-winner-box h6 {
  font-weight: bold;
  font-size: 20px;
  line-height: 25px;
  text-align: center;
  color: #a8f55c;
}

.mines-winner-box p {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
}

.mines-game-sec.winner-game {
  opacity: 0.25;
}

.mines-loss-box h6 {
  color: #e72649;
}

.mines-loss-box:before {
  position: absolute;
  width: 113px;
  height: 113px;
  background: rgba(231, 38, 73, 0.185);
  filter: blur(45px);
  content: "";
  transform: translate(-50%, -50%);
  border-radius: 50%;
}

.mines-winner-box::before {
  background: rgba(168, 245, 92, 0.15);
  filter: blur(45px);
  content: "";
  transform: translate(-50%, -50%);
  border-radius: 50%;
}

.mines-game-sec.show-all .mines-game-tiles:not(.revealed) {
  opacity: 0.5;
  visibility: visible;
  /* pointer-events: none; */
  cursor: default;
  border-width: 2px;
  border-color: rgb(44, 63, 52);
  background-size: 500% 500%;
  background-position: 75% 50%;
  background-color: rgb(44, 63, 52);
}

