}

.bet-input-button-grid svg {
  width: 15.6px;
}

.bet-input-button-grid .btn,
.bet-input-button-grid .btn:hover,
.bet-input-button-grid .btn:focus,
.bet-input-button-grid .btn-primary:not(:disabled):not(.disabled):active:focus,
.bet-input-button-grid .btn:first-child:active,
:not(.btn-check) + .btn:active {
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  --font-color: #bcbcbc;
  --background-color: #444444;
  --spinner-main-color: #c6ccd2;
  --spinner-background-color: transparent;
  --button-height: 32px;
  --padding: 0px 12px;
  --padding-if-svg: 0px 7px;
  --border-radius: 3px;
  --font-size: 16px;
  --spinner-size: 12px;
  min-height: max-content;
  min-width: max-content;
  background: #444444 !important;
  border-radius: 3px;
  border: none !important;
  margin: 0px;
  font-family: inherit;
  font-size: 16px;
  line-height: 16px;
  color: #bcbcbc !important;
  text-transform: uppercase;
  overflow: hidden;
  user-select: none;
  cursor: var(--cursor, pointer);
  --overlay-color: #070707;
  --overlay-opacity: 0;
  width: 100%;
  height: 100%;
  position: relative;
  --button-height: max-content;
  box-shadow: none;
}

.bet-input-button-grid .btn:hover,
.bet-input-button-grid .btn:active {
  background: #3b3b3b !important;
}

.bet-input-field {
  color: rgb(255, 255, 255);
  font-size: 22px;
  letter-spacing: -1px;
  line-height: 22px;
  text-align: center;
}

.bet-input-field input {
  margin: 0px;
  padding: 0px;
  box-shadow: none;
  background: transparent;
  border: none;
  border-radius: 0px;
  font-family: inherit;
  font-size: inherit;
  outline: none;
  cursor: inherit;
  display: inline;
  width: calc(10ch + 3px);
  color: #ffffff;
  text-align: center;
}

.bet-input-field input::placeholder {
  color: #ffffff !important;
}

.autobet-btn {
  position: absolute;
  width: 40px;
  height: 40px;
  right: 8px;
  top: 50%;
  cursor: pointer;
  overflow: hidden;
  opacity: 0.75;
  border-radius: 100%;
  pointer-events: auto;
  transform:rotate(0deg) scale(1);
  z-index: 999;
  margin-top: -20px;
  transition: all 0.5s;
}



